<template>
  <div>
    <el-table ref="multipleTable" :data="this.$store.getters.getdesignmangerfirstselect">
      <el-table-column prop="projectsource" label="" width="80" align="center"></el-table-column>
      <el-table-column prop="projectname" label="" width="400" align="center"></el-table-column>
      <el-table-column prop="projectnumber" label="" width="150" align="center"></el-table-column>
      <el-table-column prop="projectcode" label="" width="200" align="center"></el-table-column>
      <el-table-column prop="receivetime" label="" align="center"></el-table-column>
      <el-table-column prop="estimatetime" label="" align="center"></el-table-column>
      <el-table-column prop="deepeningdivision" label="" align="center"></el-table-column>
    </el-table>

    <div style="margin: 15px 0;"></div>

    <el-form :model="tables[0]" :rules="rules" ref="tables[0]">
      <el-table ref="multipleTable" :data="tables">
        <el-table-column prop="qiang" label="" align="center">
          <template slot-scope="scope">
            <el-form-item prop="qiang">
              <el-select v-model="scope.row.qiang" placeholder="">
                <el-option
                  v-for="item in designpersonlist"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </template>
        </el-table-column>
        <el-table-column prop="loumian" label="" align="center">
          <template slot-scope="scope">
            <el-form-item prop="loumian">
              <el-select v-model="scope.row.loumian" placeholder="">
                <el-option
                  v-for="item in designpersonlist"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </template>
        </el-table-column>
        <el-table-column prop="liang" label="" align="center">
          <template slot-scope="scope">
            <el-form-item prop="liang">
              <el-select v-model="scope.row.liang" placeholder="">
                <el-option
                  v-for="item in designpersonlist"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </template>
        </el-table-column>
        <el-table-column prop="jiedian" label="" align="center">
          <template slot-scope="scope">
            <el-form-item prop="jiedian">
              <el-select v-model="scope.row.jiedian" placeholder="">
                <el-option
                  v-for="item in designpersonlist"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </template>
        </el-table-column>
        <el-table-column prop="diaomo" label="" align="center">
          <template slot-scope="scope">
            <el-form-item prop="diaomo">
              <el-select v-model="scope.row.diaomo" placeholder="">
                <el-option
                  v-for="item in designpersonlist"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </template>
        </el-table-column>
        <el-table-column prop="louti" label="" align="center">
          <template slot-scope="scope">
            <el-form-item prop="louti">
              <el-select v-model="scope.row.louti" placeholder="">
                <el-option
                  v-for="item in designpersonlist"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button type="primary" @click="handleEdit(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tables: [
        {
          qiang: "",
          loumian: "",
          liang: "",
          jiedian: "",
          diaomo: "",
          louti: ""
        }
      ],

      rules: {
        qiang: [{ required: true, message: "", trigger: "blur" }],
        loumian: [{ required: true, message: "", trigger: "blur" }],
        liang: [{ required: true, message: "", trigger: "blur" }],
        jiedian: [{ required: true, message: "", trigger: "blur" }],
        diaomo: [{ required: true, message: "", trigger: "blur" }],
        louti: [{ required: true, message: "", trigger: "blur" }]
      },

      tablesr1: [],

      multipleSelection: []
    };
  },

  mounted() {
    this.$store.dispatch("setdesignpersonlist");
  },

  computed: {
    designpersonlist() {
      return this.$store.getters.getdesignpersonlist;
    }
  },

  methods: {
    handleEdit(row) {
      this.$refs["tables[0]"].validate(valid => {
        if (valid) {
          if (this.$store.getters.getdesignmangerfirstselect.length > 0) {
            for (
              let index = 0;
              index < this.$store.getters.getdesignmangerfirstselect.length;
              index++
            ) {
              var dem = JSON.parse(
                JSON.stringify(this.$store.getters.getdesignmangerfirstselect[index])
              );

              if (dem["tag"] === "") {
                dem["tag"] = "/";
              } else if (dem["tag"] === "") {
                dem["tag"] = "/";
              }

              var et = new Date();

              var ett =
                et.getFullYear() +
                "." +
                (et.getMonth() + 1).toString().padStart(2, "0") +
                "." +
                et
                  .getDate()
                  .toString()
                  .padStart(2, "0");

              dem["startime"] = ett;

              for (const key in row) {
                dem[key] = row[key];
              }

              this.tablesr1.push(dem);
            }

            this.$store.dispatch("setdesignmangerfirstselect", []);
            this.$store.dispatch("updatedesigndistr", this.tablesr1);
          } else {
            this.$message({
              message: "",
              type: "warning"
            });
          }
        }
      });
    }
  }
};
</script>


