<template>
  <div>
    <el-upload
      class="upload-demo"
      action="http://192.168.100.143:8082/Ex"
      accept=".xls, .xlsx"
      :before-upload="uploadsuccess"
      :on-remove="Remove"
      :limit="1"
    >
      <el-button size="small" type="primary">Excel</el-button>
    </el-upload>

    <div style="margin: 15px 0;"></div>

    <el-table
      ref="multipleTable"
      :data="newProjectData"
      @selection-change="handleSelectionChange"
      style="width: 100%;"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="projectsource" label="" width="180" align="center"></el-table-column>
      <el-table-column prop="projectname" label="" width="300" align="center"></el-table-column>
      <el-table-column prop="projectnumber" label="" align="center"></el-table-column>
      <el-table-column prop="projectcode" label="" align="center"></el-table-column>
      <el-table-column prop="receivetime" label="" align="center"></el-table-column>
      <el-table-column prop="estimatetime" label="" align="center"></el-table-column>
      <el-table-column prop="department" label="" align="center"></el-table-column>
    </el-table>

    <div style="margin: 15px 0;"></div>
    <div>
      <!-- <el-checkbox v-model="checked" @change="handleCheckAllChange"></el-checkbox> -->
      <el-select v-model="value" placeholder="" :disabled="disabled" style="position:relative;">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-button type="primary" @click="distribution"></el-button>
      <el-button type="primary" @click="reset"></el-button>
    </div>
    <div style="margin: 15px 0;"></div>
    <div>
      <el-button type="primary" style="margin-left:47%" @click="CreateProject"></el-button>
    </div>
  </div>
</template>

<script>
import XLSX from "xlsx";
var that = this;
export default {
  data() {
    return {
      newProjectData: [],
      checked: false,
      disabled: false,

      options: [
        {
          value: "1",
          label: "1"
        },
        {
          value: "2",
          label: "2"
        },
        {
          value: "3",
          label: "3"
        },
        {
          value: "4",
          label: "4"
        },
        {
          value: "5",
          label: "5"
        },
        {
          value: "6",
          label: "6"
        }
      ],
      value: "",

      multipleSelection: []
    };
  },
  methods: {
    uploadsuccess(file) {
      var that = this;
      var wss = [];
      const fileReader = new FileReader();
      fileReader.readAsBinaryString(file); //Excel
      fileReader.onload = function(ev) {
        try {
          const data = ev.target.result;
          const workbook = XLSX.read(data, {
            type: "binary"
          });
          const wsname = workbook.SheetNames[0]; //
          wss = XLSX.utils.sheet_to_json(workbook.Sheets[wsname]);

          wss.map((value, index, array) => {
            var reg = new RegExp("[.-/]");
            var rtt = "";
            var ett = "";

            if (
              !reg.test(value[""]) &&
              !reg.test(value[""])
            ) {
              var rt = new Date((value[""] - 25569) * 24 * 3600000); //Excel1900.1.1Date1970.1.125569
              rtt =
                rt.getFullYear() +
                "." +
                (rt.getMonth() + 1).toString().padStart(2, "0") +
                "." +
                rt
                  .getDate()
                  .toString()
                  .padStart(2, "0");

              var et = new Date((value[""] - 25569) * 24 * 3600000);
              ett =
                et.getFullYear() +
                "." +
                (et.getMonth() + 1).toString().padStart(2, "0") +
                "." +
                et
                  .getDate()
                  .toString()
                  .padStart(2, "0");
            } else if (
              !reg.test(value[""]) &&
              reg.test(value[""])
            ) {
              var rt = new Date((value[""] - 25569) * 24 * 3600000); //Excel1900.1.1Date1970.1.125569
              rtt =
                rt.getFullYear() +
                "." +
                (rt.getMonth() + 1).toString().padStart(2, "0") +
                "." +
                rt
                  .getDate()
                  .toString()
                  .padStart(2, "0");

              ett = value[""];
            } else if (
              reg.test(value[""]) &&
              !reg.test(value[""])
            ) {
              rtt = value[""];
              var et = new Date((value[""] - 25569) * 24 * 3600000);
              ett =
                et.getFullYear() +
                "." +
                (et.getMonth() + 1).toString().padStart(2, "0") +
                "." +
                et
                  .getDate()
                  .toString()
                  .padStart(2, "0");
            } else {
              rtt = value[""];
              ett = value[""];
            }
            that.newProjectData.push({
              projectsource: value[""],
              projectname: value[""],
              projectnumber: value[""],
              projectcode: value[""],
              receivetime: rtt,
              estimatetime: ett,
              department: null,
              deepeningdivision: null,
              tag: "",
              clientname: null,
              marketstaffwithcall: null,
              templatesystem: null,
              whetherprefab: null,
              layerheigh: null,
              projectdifficulty: null,
              signaturetime: null,
              designmanager: null,
              qiang: null,
              liang: null,
              louti: null,
              jiedian: null,
              loumian: null,
              diaomo: null,
              startime: null,
              realtime: null
            });
          });
        } catch (e) {
          console.log(e);
          return;
        }
      };
    },
    Remove() {
      this.newProjectData = [];
    },
    handleCheckAllChange(val) {
      if (val) {
        this.disabled = true;
        if (this.newProjectData !== []) {
          this.newProjectData.forEach(element => {
            element["department"] = "1";
          });
        }
      } else {
        this.disabled = false;
        if (this.newProjectData !== []) {
          this.newProjectData.forEach(element => {
            element["department"] = "";
          });
        }
      }
    },
    distribution() {
      if (this.multipleSelection !== []) {
        this.multipleSelection.forEach(element => {
          var index = this.newProjectData.indexOf(element);
          if (index > -1) {
            this.newProjectData[index]["department"] = this.value;
          }
        });
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    reset() {
      for (let index = 0; index < this.newProjectData.length; index++) {
        this.newProjectData[index]["department"] = "";
      }
    },
    CreateProject() {
      if (this.newProjectData.length > 0) {
        //
        var isnull = false;
        for (let index = 0; index < this.newProjectData.length; index++) {
          if (
            this.newProjectData[index]["department"] === null ||
            this.newProjectData[index]["department"] === ""
          ) {
            isnull = true; //
          }
        }
        if (!isnull) {
          this.$store.dispatch("creatprojectaxios", this.newProjectData);
        } else {
          this.$message({
            message: "",
            type: "warning"
          });
        }
      } else {
        this.$message({
          message: "",
          type: "warning"
        });
      }
    }
  }
};
</script>


