import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Manage from '@/components/Manage';
import projectInformation from '@/components/projectInformation';
import ProjectList from '@/components/ProjectList';
import creatproject from '@/components/creatproject';
import allocationitem from '@/components/allocationitem';
import receiveconfirmation from '@/components/receiveconfirmation';
import deep from '@/components/deep';
import deeping from '@/components/deeping';
import deepse from '@/components/deepse';
import deepingse from '@/components/deepingse';
import realdeep from '@/components/realdeep';
import designmanager from '@/components/designmanager';
import designmanagerdistr from '@/components/designmanagerdistr';

import realdesignmanager from '@/components/realdesignmanager';
import wrongfill from '@/components/wrongfill';

import realdesignmanagerse from '@/components/realdesignmanagerse';
import realdesignmanagersese from '@/components/realdesignmanagersese';
import creatprojectlist from '@/components/creatprojectlist';
import creatprojectchange from '@/components/creatprojectchange';
import designperson from '@/components/designperson';
import designpersonse from '@/components/designpersonse';
import realdesignperson from '@/components/realdesignperson';
import bbs from '@/components/bbs';

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',//
      name: 'Login',
      component: Login,
      meta: {
        root: "Login"
      }
    },
    {
      path: '/Manage',//
      name: 'Manage',
      component: Manage,
      meta: {
        position: [{ path: '/Manage', name: '' }]
      },
      children: [
        {
          path: '/bbs',
          name: 'bbs',//
          component: bbs,
          meta: {
            position: [{ path: '/bbs', name: '' }]
          }
        },
        {
          path: '',
          name: 'ProjectList',//
          component: ProjectList,
          meta: {
            position: [{ path: '/Manage', name: '' }]
          }
        },
        {
          path: '/projectInformation',//
          name: 'projectInformation',
          component: projectInformation,
          meta: {
            position: [{ path: '/Manage', name: '' }, { path: '/projectInformation', name: '' }]
          }
        },
        {
          path: '/creatproject',//
          name: 'creatproject',
          component: creatproject,
          meta: {
            position: [{ path: '/creatproject', name: '' }]
          }
        },
        {
          path: '/creatprojectlist',//
          name: 'creatprojectlist',
          component: creatprojectlist,
          meta: {
            position: [{ path: '/creatprojectlist', name: '' }]
          }
        },
        {
          path: '/creatprojectchange',//
          name: 'creatprojectchange',
          component: creatprojectchange,
          meta: {
            position: [{ path: '/creatprojectlist', name: '' }, { path: '/creatprojectchange', name: '' }]
          }
        },

        {
          path: '/receiveconfirmation',//
          name: 'receiveconfirmation',
          component: receiveconfirmation,
          meta: {
            position: [{ path: '/receiveconfirmation', name: '' }]
          }
        },
        {
          path: '/allocationitem',//
          name: 'allocationitem',
          component: allocationitem,
          meta: {
            position: [{ path: '/allocationitem', name: '' }]
          }
        },
        {
          path: '/deep',//
          name: 'deep',
          component: deep,
          meta: {
            position: [{ path: '/deep', name: '' }]
          }
        },
        {
          path: '/deeping',//
          name: 'deeping',
          component: deeping,
          meta: {
            position: [{ path: '/deep', name: '' }, { path: '/deeping', name: '' }]
          }
        },

        {
          path: '/deepse',//
          name: 'deepse',
          component: deepse,
          meta: {
            position: [{ path: '/deepse', name: '' }]
          }
        },
        {
          path: '/deepingse',//
          name: 'deepingse',
          component: deepingse,
          meta: {
            position: [{ path: '/deepse', name: '' }, { path: '/deeping', name: '' }]
          }
        },
        {
          path: '/realdeep',//
          name: 'realdeep',
          component: realdeep,
          meta: {
            position: [{ path: '/realdeep', name: '' }]
          }
        },
        {
          path: '/designmanager',//
          name: 'designmanager',
          component: designmanager,
          meta: {
            position: [{ path: '/designmanager', name: '' }]
          }
        },
        {
          path: '/designmanagerdistr',//
          name: 'designmanagerdistr',
          component: designmanagerdistr,
          meta: {
            position: [{ path: '/designmanager', name: '' }, { path: '/designmanagerdistr', name: '' }]
          }
        },
        {
          path: '/realdesignmanager',//
          name: 'realdesignmanager',
          component: realdesignmanager,
          meta: {
            position: [{ path: '/realdesignmanager', name: '' }]
          }
        },
        {
          path: '/wrongfill',//
          name: 'wrongfill',
          component: wrongfill,
          meta: {
            position: [{ path: '/realdesignmanager', name: '' }, { path: '/wrongfill', name: '/' }]
          }
        },
        {
          path: '/realdesignmanagerse',//
          name: 'realdesignmanagerse',
          component: realdesignmanagerse,
          meta: {
            position: [{ path: '/realdesignmanagerse', name: '' }]
          }
        },
        {
          path: '/realdesignmanagersese',//
          name: 'realdesignmanagersese',
          component: realdesignmanagersese,
          meta: {
            position: [{ path: '/realdesignmanagerse', name: '' }, { path: '/realdesignmanagersese', name: '' }]
          }
        },
        {
          path: '/designperson',//
          name: 'designperson',
          component: designperson,
          meta: {
            position: [{ path: '/designperson', name: '' }]
          }
        },
        {
          path: '/realdesignperson',//
          name: 'realdesignperson',
          component: realdesignperson,
          meta: {
            position: [{ path: '/realdesignperson', name: '' }]
          }
        },
        {
          path: '/designpersonse',//
          name: 'designpersonse',
          component: designpersonse,
          meta: {
            position: [{ path: '/designperson', name: '' }, { path: '/designpersonse', name: '' }]
          }
        }
      ]
    }
  ]
})





