<template>
  <div>
    <el-table
      ref="multipleTable"
      :data="this.$store.getters.getdesigntwo"
      @selection-change="handleSelectionChange"
      style="width: 100%; font-size:9px;"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="projectsource" label="" width="70" align="center"></el-table-column>
      <el-table-column prop="projectname" label="" width="200" align="center"></el-table-column>
      <el-table-column prop="projectnumber" label="" width="70" align="center"></el-table-column>
      <el-table-column prop="projectcode" label="" align="center"></el-table-column>
      <el-table-column prop="receivetime" label="" align="center"></el-table-column>
      <el-table-column prop="estimatetime" label="" align="center"></el-table-column>
      <el-table-column prop="deepeningdivision" label="" width="70" align="center"></el-table-column>
      <el-table-column prop="tag" label="" width="70" align="center"></el-table-column>
      <el-table-column prop="qiang" label="" width="70" align="center"></el-table-column>
      <el-table-column prop="loumian" label="" width="70" align="center"></el-table-column>
      <el-table-column prop="liang" label="" width="70" align="center"></el-table-column>
      <el-table-column prop="jiedian" label="" width="70" align="center"></el-table-column>
      <el-table-column prop="diaomo" label="" width="70" align="center"></el-table-column>
      <el-table-column prop="louti" label="" width="70" align="center"></el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="dowmDeep(scope.row)">
            
            <i class="el-icon-download"></i>
          </el-button>
        </template>
      </el-table-column>
      <el-table-column label="" width="100" align="center">
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="mini"
            @click="handleEdit(scope.row)"
            style="font-size:9px;"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      multipleSelection: []
    };
  },
  methods: {
    handleEdit(row) {
      if (this.multipleSelection.length === 0) {
        this.$store.dispatch("setdesignmangerselecttwo", [row]);
      } else {
        this.$store.dispatch(
          "setdesignmangerselecttwo",
          this.multipleSelection
        );
      }

      this.$router.push({ path: "/realdesignmanagersese" });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    dowmDeep(row) {
      var urldown = "http://192.168.100.143:8082/download/" + row.projectcode;

      var file = row["deepfile"];
      var fileName = file.substring(file.indexOf("-") + 1);

      console.log(row);

      this.$axios({
        method: "post",
        url: urldown,
        responseType: "blob"
      }).then(function(response) {
        const blob = new Blob([response.data]);
        if ("download" in document.createElement("a")) {
          const link = document.createElement("a");
          link.download = fileName;
          link.style.display = "none";
          link.href = URL.createObjectURL(blob);
          document.body.appendChild(link);
          link.click(); // 
          URL.revokeObjectURL(link.href); // url
          document.body.removeChild(link); // 
        } else {
          navigator.msSaveBlob(blob, name);
        }
      });
    }
  }
};
</script>


