    _setplannerchangselect(state, changselect) {//
        state.plannerchangselect = changselect;
    },
    _setplannercanchange(state, plannercanchange) {//

        for (let index = 0; index < plannercanchange.length; index++) {
            if (plannercanchange[index]["tag"] === "" ||
                plannercanchange[index]["tag"] === "/" ||
                plannercanchange[index]["tag"] === "") {
                state.plannercanchange[plannercanchange[index]["projectcode"]] = true;//
            } else {
                state.plannercanchange[plannercanchange[index]["projectcode"]] = false;//
            }
        }

    },
    _setcreatcompletelist(state, creatcompletelist) {//
        state.creatcompletelist = creatcompletelist
    },
    //--

    //----------
    _setprojectInfodb(state, infodb) {//


        state.projectInfodb = state.projectdb.filter(
            data => data["projectcode"] === infodb[0]["projectcode"]
        );

    },

    _setprojectInfo(state, info) {//
        state.projectInfo = info;
    },

    _setprojectInfodbwrong(state, infodb) {//

        state.projectInfodbwronglook = state.projectdbwrong.filter(
            data => data["projectcode"] === infodb[0]["projectcode"]
        );

    },
    //----------

    //----------
    _updateprojectInfodb(state, infodb) {//
        if (sessionStorage.getItem("projectcode") != null) {
            state.projectInfodb = infodb.filter(
                data => data["projectcode"] === sessionStorage.getItem("projectcode")
            );
        }
    },
    _updateprojectInfo(state, info) {//
        if (sessionStorage.getItem("projectcode") != null) {
            state.projectInfo = info.filter(
                data => data["projectcode"] === sessionStorage.getItem("projectcode")
            );
        }
    },
    //----------

