    setplannerchangselect(context, changselect) {//
        context.commit('_setplannerchangselect', changselect);
    },
    updateCreatProject(context, updatedb) {
        console.log(updatedb);

        axios({
            method: "post",
            url: "http://zwwwwww.applinzi.com/changProject",
            data: updatedb,
            headers: {
                token: sessionStorage.getItem("token")
            }
        })
            .then(function (response) {
                console.log(response);

                if (response.data === "success") {
                    Message({
                        message: "",
                        type: "success"
                    });
                }
            })
            .catch(e => {
                console.log(e);
            });
    },
    //--

    setprojectInfo(context, Info) {//
        context.commit('_setprojectInfodb', Info);//
        context.commit('_setprojectInfo', Info);// 
        context.commit('_setprojectInfodbwrong', Info);// 
    },


