      <el-table-column prop="jiedian" label="" align="center"></el-table-column>
      <el-table-column prop="diaomo" label="" align="center"></el-table-column>
      <el-table-column prop="louti" label="" align="center"></el-table-column>

      <el-table-column label="" width="110" align="center">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.row)">
            
            <i class="el-icon-download"></i>
          </el-button>
        </template>
      </el-table-column>

      <el-table-column label="/" align="center">
        <template slot-scope="scope">
          <el-button size="mini" @click="Fillwrong(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
    };
  },
  methods: {
    Fillwrong(row) {
      this.$store.dispatch("setselectwrong", [row]);
      this.$router.push({ path: "/wrongfill" });
    },
    handleEdit(row) {
      var urldown = "http://192.168.100.143:8082/downloads/" + row.projectcode;

      var file = row["designfile"];
      var fileName = file.substring(file.indexOf("-") + 1);

      this.$axios({
        method: "post",
        url: urldown,
        responseType: "blob"
      }).then(function(response) {
        const blob = new Blob([response.data]);
        if ("download" in document.createElement("a")) {
          const link = document.createElement("a");
          link.download = fileName;
          link.style.display = "none";
          link.href = URL.createObjectURL(blob);
          document.body.appendChild(link);
          link.click(); // 
          URL.revokeObjectURL(link.href); // url
