<template>
  <div>
    <el-table
      ref="multipleTable"
      :data="this.$store.getters.getdeep"
      @selection-change="handleSelectionChange"
      style="width: 100%"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="projectsource" label="" width="180" align="center"></el-table-column>
      <el-table-column prop="projectname" label="" width="300" align="center"></el-table-column>
      <el-table-column prop="projectnumber" label="" align="center"></el-table-column>
      <el-table-column prop="projectcode" label="" align="center"></el-table-column>
      <el-table-column prop="receivetime" label="" align="center"></el-table-column>
      <el-table-column prop="estimatetime" label="" align="center"></el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-button size="mini"  @click="handleEdit(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<style scoped>
</style>

<script>
export default {
  data() {
    return {
      multipleSelection: []
    };
  },
  methods: {
    handleEdit(row) {

      if (this.multipleSelection.length === 0) {
        this.$store.dispatch("setdeepselect", [row]);
      } else {
        this.$store.dispatch("setdeepselect", this.multipleSelection);
      }
      this.$router.push({ path: "/deeping" });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    }
  }
};
</script>


