<template>
  <div>
    <el-table
      ref="multipleTable"
      :data="newProjectData"
      @selection-change="handleSelectionChange"
      style="width: 100%;"
    >
      <el-table-column prop="projectsource" label="" width="180" align="center"></el-table-column>
      <el-table-column prop="projectname" label="" width="300" align="center"></el-table-column>
      <el-table-column prop="projectnumber" label="" align="center"></el-table-column>
      <el-table-column prop="projectcode" label="" align="center"></el-table-column>
      <el-table-column prop="receivetime" label="" align="center"></el-table-column>
      <el-table-column prop="estimatetime" label="" align="center"></el-table-column>
      <el-table-column prop="department" label="" align="center"></el-table-column>
      <el-table-column prop="deepeningdivision" label="" align="center"></el-table-column>
       <el-table-column
        prop="tag"
        label=""
        width="80"
        align="center"
        :filters="this.$store.getters.gettag"
        :filter-method="filterTag "
      ></el-table-column>

    </el-table>
  </div>
</template>

<script>
var that = this;

export default {
  data() {
    return {
      multipleSelection: []
    };
  },

  mounted() {},
  computed: {
    newProjectData() {
      return this.$store.getters.getdeepdistrcomplete;
    }
  },

  methods: {
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
     filterTag(value, row) {
      return row.tag === value;
    },
  }
};
</script>


